<template>
	<view class="box">
		<uni-icons type="clear" size="30" color="#000" @click="close" class="icon"></uni-icons>
		<swiper :current="current">
			<swiper-item v-for="(tag,index) in arr" :key="index">
				<view v-for="(tag,index) in arr" :key="index" class="item">
					<image :src="tag.url" mode="aspectFit" v-if="tag.type==1"></image>
					<video :src="tag.url" controls v-if="tag.type==2"></video>
				</view>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
	export default {
		name:"prevImgVideo",
		props:{
			arr:{
				type:Array,
				default:1
			},
			current:{
				// 当前显示的文件的小标
				type:Number,
				default:0
			}
		},
		data() {
			return {
			};
		},
		mounted() {
		},
		methods:{
			close(){
				this.$emit('closePrev')
			}
		}
	}
</script>

<style lang="scss" scoped>
.box{
	width: 100vh;
	height: 100vh;
	position: relative;
	swiper{
		width: 100%;
		height: 100%;
	}
	.item{
		width: 100%;
		height: 100%;
		video,image{
			width: 100%;
			height: 100%;
		}
	}
}
.icon{
	position: fixed;
	top:100rpx;
	right:30rpx;
	z-index:10;
}
</style>
